@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.6rem;
  --typing-font: 3rem;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Raleway';
}
.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav {
  padding: 60px 30px 0px 30px;
  flex: 1.5;
  flex: var(--sidebar-flex);
  background: rgb(238, 103, 143);
  background: linear-gradient(
    180deg,
    rgba(238, 103, 143, 1) 50%,
    rgba(132, 124, 252, 1) 80%
   

  );
}
.condiv {
  padding: 100px;
  background-color: #f4f5f9;
  flex: 6;
  flex: var(--condiv-flex);
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.profilepic {
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}
.typingeffect {
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

nav ul {
  font-size: 1.6rem;
  font-size: var(--fontsize-nav);
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li {
  margin: 40px;
}

a {
  text-decoration: none;
  color: white;
}

.subtopic {
  margin: 10px 10px 30px 10px !important;
}
p {
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,
h2,
h3,
h4,
p {
  color: #000000;
  margin: 10px;
}
.social {
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}
.social i {
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover {
  color: #ffffff;
}
.widecard {
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
}
.widecard:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.active {
  font-weight: bolder;
}
.skills ul li {
  list-style-type: none;
  margin: 30px;
}


@media all and (max-width: 450px) {
  :root {
    --fontsize-nav: 1rem;
    --sidebar-flex: 0;
    --typing-font: 2rem;
  }
  .App {
    flex-direction: column;
  }
  .sidebar {
    padding: 30px;
    background: rgb(238, 103, 143);
    background: linear-gradient(
      90deg,
      rgba(238, 103, 143, 1) 50%,
      rgba(132, 124, 252, 1) 80%
    );
  }
  .condiv {
    padding: 50px;
  }
  nav ul {
    display: flex;
  }
  nav ul li {
    margin: 10px;
  }
  .social {
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }
  .social i {
    padding-right: 0px;
  }


  
}

* {
    box-sizing: border-box;
    font-family: "Helvetica";
    
  }
  
  
  
  /* Mobile First */
  form {
    margin: 30px;
    padding: 0;
  }
  
  fieldset {
    border: none;
    margin: 0;
    padding: 0;
  }
  
  .form-row {
    margin: 10px;
    padding: 8px 13px 2px 17px;
    border-bottom: 1px solid #e6eef8;
  }
  
  .form-row:last-child {
    border: none;
  }
  
  .form-row label {
    color: #424770;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: .43px;
    line-height: 26px;
    text-transform: uppercase;
    display: block;
  }
  
  .form-row input,
  .form-row textarea {
    display: block;
    border: none;
    outline: none;
    width: 100%;
    border-radius: 4px;
    color: #32325d;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    padding: 5px 20px 8px 0;
  }
  
  .submit-row {
    text-align: right;
    padding: 12px 10px 13px 0;
  }
  
  .form-submit {
    -webkit-appearance: none;
    outline: none;
    border: none;
    cursor: pointer;
    font-family: inherit;
  
    color: #fff;
    background: #6772e5;
  
    height: 40px;
    white-space: nowrap;
    display: inline-block;
    line-height: 40px;
    padding: 0 14px;
    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
    border-radius: 4px;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: .025em;
    text-decoration: none;
    transition: all .15s ease;
  }
  
  .form-submit:active {
    color: #e6ebf1;
    background-color: #555abf;
    transform: translateY(1px);
    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
  }
  
  
  
  /* Non Mobile */
  @media (min-width: 670px) {
    form {
      padding: 10px 18px 20px 24px;
     
    }
  
    .form-row {
      padding: 4px 0;
      max-width: 600px;
      display: flex;
      flex-direction: row;
      border-bottom: none;
     
    }
  
    .form-row label {
      font-size: 17px;
      font-weight: 500;
      margin-right: 20px;
      align-self: center;
      flex: 30% 1;
      letter-spacing: normal;
      text-transform: none;
    }
  
    .form-row:last-child label {
      align-self: flex-start;
      padding-top: 7px;
    }
  
    .form-row input,
    .form-row textarea {
      align-self: center;
      padding: 5px 20px 8px 13px;
      flex: 70% 1;
      width: auto;
      border: 1px solid #e6eef8;
    }
  
    .form-row textarea {
      padding: 7px 20px 7px 13px;
      min-height: 100px;
    }
  
    .submit-row {
      padding: 12px 0 0;
    }
  }
input.invalid__value,
textarea.invalid__value {
  border-color: red;
}

input.invalid__value::-webkit-input-placeholder, textarea.invalid__value::-webkit-input-placeholder {
  color: red;
}

input.invalid__value:-ms-input-placeholder, textarea.invalid__value:-ms-input-placeholder {
  color: red;
}

input.invalid__value::placeholder,
textarea.invalid__value::placeholder {
  color: red;
}


